import * as React from "react";
import { hot } from "react-hot-loader";
import { Route, Switch, Link, useHistory } from "react-router-dom";
import MainLayout from "./layout/MainLayout";

import "./App.scss";

import UserProvider from "./providers/UserProvider";
import LoginUIProvider from "./providers/LoginUIProvider";

import MapView from "./views/GroupEvents/MapView";
import MyProfileView from "./views/MyProfileView";
import HelpView from "./views/HelpView";
import OtherProfileView from "./views/OtherProfileView";
import SurveyView from "./views/SurveyView";
import FinishSurveyView from "./views/FinishSurveyView";
import MeditationMapView from "./views/Meditations/MapView";
import DownloadView from "./views/DownloadView";
import { useEffect } from "react";

import { analytics } from "./amplitude";
import CreateEventLanding from "./views/Landings/CreateEventLandingView";
import CreateEventFastView from "./views/Landings/CreateEventFastView";
import LoginLandingView from "./views/Landings/LoginLandingView";
import FindGroupEvents from "./views/Landings/FindGroupEventsView";

const App = () => {
  const history = useHistory();

  useEffect(() => {
    return history.listen((location) => {
      analytics
        .getInstance()
        .logEvent("PageChanged", { newLocation: location.pathname });
    });
  }, [history]);

  return (
    <UserProvider>
      <LoginUIProvider>
        <MainLayout>
          <Switch>
            <Route exact path="/">
              <MapView />
            </Route>
            <Route exact path="/subscribe">
              <DownloadView />
            </Route>
            <Route exact path="/find-group-event-landing">
              <FindGroupEvents />
            </Route>
            <Route exact path="/login-landing">
              <LoginLandingView />
            </Route>
            <Route exact path="/create-event-landing">
              <CreateEventLanding />
            </Route>
            <Route exact path="/create-event-fast">
              <CreateEventFastView />
            </Route>
            <Route exact path="/download">
              <DownloadView />
            </Route>
            <Route exact path="/meditation-places">
              <MeditationMapView />
            </Route>
            <Route exact path="/survey">
              <SurveyView />
            </Route>
            <Route exact path="/finish-survey">
              <FinishSurveyView />
            </Route>
            <Route path="/events/:eventId">
              <MapView />
            </Route>
            <Route exact path="/profile/">
              <MyProfileView />
            </Route>
            <Route exact path="/profile/:profileId">
              <OtherProfileView />
            </Route>
            <Route exact path="/help">
              <HelpView />
            </Route>
            <Route>
              <div>
                Not found&nbsp;<Link to="/">Go to root</Link>
              </div>
            </Route>
          </Switch>
        </MainLayout>
      </LoginUIProvider>
    </UserProvider>
  );
};

declare let module: Record<string, unknown>;

export default hot(module)(App);
